:root {
    --color-grey: #9e9e9e73;
    --color-accent: rgb(0, 188, 212);
    --bottom-nav-height: 57px;
    --app-header-height: 64px;
    --app-max-width: 1100px;
    --app-wrapper-height: calc(100vh - var(--app-header-height) - var(--bottom-nav-height));
    --app-padding: 5px;
}



