body {
  margin: 0;
  font-family: sans-serif;
  min-height: 100%;
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p, div, span {
  font-weight: 300;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 50px white inset;
}

a:-webkit-any-link {
  color: inherit;
  text-decoration: none;
}



